import React, {Component} from 'react';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { openDialog }from '../actions/index';
class GlobalHeader extends Component {
    constructor(props){
        super(props);
    }

    render() {
        return <div className="global-header">
            <h1><i className="fas fa-store"></i>My Store Application</h1>
        </div>;
    }

}

//use this to call action creater in class by props
function mapDispatchToProps(dispatch) {
    return bindActionCreators({openDialog: openDialog}, dispatch);
}

//use this to get app state in class by props
function mapStateToProps(state) {
    return {};//just maintain only one level of state/ otherwise you can't get the child obj props
}

export default connect(mapStateToProps, mapDispatchToProps)(GlobalHeader);


